table {
  border-collapse: collapse;
  width: calc(100% - 40px);
  margin: 20px; }
  table td,
  table th {
    border: 1px solid #ddd;
    padding: 8px; }
    table td:first-of-type,
    table th:first-of-type {
      text-align: left; }
  table tr:nth-child(even) {
    background-color: #f2f2f2; }
  table tr:hover {
    background-color: #ddd; }
  table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #eff2e9; }



